body {
    line-height: 1.5;
}
h1 {
    text-align: center;
    margin: 0px 0px 10px;
}
h2 {
    text-align: center;
    margin: 0px 0px 15px;
}
.microcosm  {
}
.abstract {
    text-align: center;
}
.btn-wrap {
    margin: 12px;
}
.btn {
    width: 200px;
    margin: 10px;
    padding: 8px;
    color: #f05279;
    border: 2px solid #f05279;
    text-decoration: none;
    transition: background-color 0.2s ease-out, color 0.2s ease-out;
}
.btn:hover {
    background: #f05279;
    color: #fff;
    transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

.item {
    padding: 12px;
    box-sizing: border-box;
}
.item-inner {
    border: 2px solid #ef571b;
    padding: 8px;
    min-height: 12em;
}
.item-inner .head {
    text-align: center;
    font-weight: bold;
    border-bottom: 1px solid #ef8e1b;
}
.item-inner .body {
    margin-top: 1em;
}
.icon {
    font-size:3em;
    color: #f05279;
}
.imgs {
    width: 100%;
    text-align: center;
}
.imgs a {
    margin-left: 10px;
    margin-right: 10px;
}
.center {
    text-align: center;
}
.section {
    padding-top: 20px;
    padding-bottom: 30px;
}
.color {
    background: #f2f2f2;
}
.color2 {
    background: #f8f8f8;
}
.section-inner {
    width: 100%;
    margin: 0 auto;
}
dt {
    font-weight: bold;
}
dt.lineup {
    margin-top: 4px;
    border-bottom: 1px dotted #ef571b;
}
.dl {
    border: 2px solid #ef571b;
    padding: 12px;
    margin-bottom: 1em;
}
.dl h3 {
    margin-top: 0px;
}
.dl-link {
    text-align: right;
}
.faq {
    margin: 4px 0px;
}
.footer {
    padding: 15px 0px;
    text-align: center;
}

#page-top {
    position: fixed;
    bottom: 14px;
    right: 20px;
}
#page-top a {
    text-decoration: none;
    width: 50px;
    padding: 4px 0;
    color: #f05279;
    border: 2px solid #f05279;
    font-size: 1.4em;
    text-align: center;
    display: block;
    transition: background-color 0.2s ease-out, color 0.2s ease-out;
}
#page-top a:hover {
    text-decoration: none;
    background: #f05279;
    color: #fff;
    transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

@media(min-width: 540px) {
    .section-inner {
        width: 800px;
        margin: 0 auto;
        padding-left: 10px;
        padding-right: 10px;
    }
}
